<template>
	<div class="container">
		<home-button />
		<back-button target="/test" />
		<speaker-button @click="playAudio" />
		<score-counter :current="currentAnswer" :max="maxAnswer" />
		<div v-if="!isClickable" class="content-overlay"></div>
		<div class="content-container">
			<div
				v-for="(item, index) in currentRandom"
				:key="item + index"
				class="image"
				:class="isAnswered ? (item.status ? 'correct' : 'wrong') : ''"
			>
				<img :src="item.src" @click="answerHandler(index)" />
			</div>
		</div>
	</div>
</template>

<script>
	import HomeButton from '../../components/HomeButton.vue';
	import BackButton from '../../components/BackButton.vue';
	import SpeakerButton from '../../components/SpeakerButton.vue';
	import ScoreCounter from '../../components/ScoreCounter.vue';

	import ChooseAudio from '../../assets/audio/Избери.mp3';
	import CorrectAudio from '../../assets/audio/Браво.mp3';
	import IncorrectAudio from '../../assets/audio/НеПозна.mp3';

	import alphabetDetails from '../../utils/alphabetDetails';

	export default {
		components: {
			HomeButton,
			BackButton,
			SpeakerButton,
			ScoreCounter,
		},
		data() {
			return {
				randomAlphabets: [],
				currentRandom: [],
				currentAlphabetAudio: null,
				chooseAudio: new Audio(ChooseAudio),
				correctAudio: new Audio(CorrectAudio),
				incorrectAudio: new Audio(IncorrectAudio),
				isAnswered: false,
				maxAnswer: 3,
				currentAnswer: 0,
				correctAnswer: 0,
				correctAnswerLetter: '',
				correctAnswerCounter: 0,
				isClickable: true,
			};
		},
		methods: {
			init() {
				// Get all of the alphabets
				this.randomAlphabets = alphabetDetails.map((item) => item.alphabet);

				this.getNext();
			},
			getNext() {
				this.isClickable = true;
				// Reset answer
				this.isAnswered = false;

				// Generate the question
				const { answerNumber, answerLetter, answers } = this.generateQuestion(
					5
				);
				this.correctAnswer = answerNumber;
				this.correctAnswerLetter = answerLetter;

				// Load correct answer audio
				this.currentAlphabetAudio = new Audio(
					require(`../../assets/audio/АБВ/${answers[answerNumber]}.mp3`)
				);

				// Load all answer audio
				this.currentRandom = answers.map((answer, index) => ({
					src: require(`../../assets/letters/${answer}.png`),
					status: index === this.correctAnswer,
				}));

				this.playAudio();
			},
			answerHandler(index) {
				this.isClickable = false;
				this.isAnswered = true;
				this.randomAlphabets = this.randomAlphabets.filter(
					(item) => !(item === this.correctAnswerLetter)
				);
				if (index === this.correctAnswer) {
					this.correctAnswerCounter++;
					this.correctAudio.play();
				} else {
					this.incorrectAudio.play();
				}

				// If currentAnswer bigger than maxAnswer, re-route to result
				this.currentAnswer++;
				if (this.currentAnswer == this.maxAnswer) {
					setTimeout(() => {
						this.$router.push({
							path: '/test/result',
							query: {
								correct: this.correctAnswerCounter,
								incorrect: this.maxAnswer - this.correctAnswerCounter,
							},
						});
					}, 1000);
				}
			},
			arrayShuffle(array, arrayLength) {
				let tempArr = [...array];
				const randomNumber = Math.floor(Math.random() * arrayLength);

				const oldNumber = array[0];

				const tempNumber = tempArr[randomNumber];
				tempArr[randomNumber] = oldNumber;
				tempArr[0] = tempNumber;

				return tempArr;
			},
			generateQuestion(number) {
				let answers = [];
				while (answers.length < number) {
					let randomIndex = Math.floor(
						Math.random() * this.randomAlphabets.length
					);
					let randomAlphabet = this.randomAlphabets[randomIndex];
					if (answers.indexOf(randomAlphabet) === -1)
						answers.push(randomAlphabet);
				}
				let answerNumber = Math.floor(Math.random() * answers.length);
				let answerLetter = answers[answerNumber];
				return {
					answerNumber,
					answerLetter,
					answers,
				};
			},
			playAudio() {
				this.chooseAudio.play();
			},
		},
		created() {
			this.chooseAudio.addEventListener('ended', () => {
				this.currentAlphabetAudio.play();
			});
			this.correctAudio.addEventListener('ended', () => {
				setTimeout(() => {
					if (!(this.currentAnswer == this.maxAnswer)) {
						this.getNext();
					}
				}, 3000);
			});
			this.incorrectAudio.addEventListener('ended', () => {
				setTimeout(() => {
					if (!(this.currentAnswer == this.maxAnswer)) {
						this.getNext();
					}
				}, 3000);
			});
		},
		mounted() {
			this.init();
		},
	};
</script>

<style scoped>
	.container {
		width: 100vw;
		height: 100vh;
		padding-top: 5vh;
		display: flex;
		flex-direction: column;
		align-items: center;
		background-image: url('../../assets/yellow-bg.png');
		background-repeat: no-repeat;
		background-position: center center;
		background-size: cover;
		overflow: scroll;
	}

	.content-overlay {
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		z-index: 10;
		cursor: not-allowed;
	}

	.content-container {
		display: flex;
		justify-content: center;
		align-content: center;
		width: 100%;
		height: 100%;
	}

	.image {
		display: flex;
		justify-content: center;
		align-items: center;
	}

	.content-container img {
		max-width: 100%;
	}

	.content-container img:hover {
		cursor: pointer;
		opacity: 0.7;
	}

	.correct {
		position: relative;
		animation: upAndDown 1s alternate infinite;
	}

	.wrong {
		opacity: 0.2;
	}

	@keyframes upAndDown {
		0% {
			top: 0;
		}
		50% {
			top: 20px;
		}
		100% {
			top: -20px;
		}
	}
</style>
