<template>
	<img
		class="btn-back"
		src="../assets/Backarrow.png"
		alt="Back Button"
		@click="goBack"
	/>
</template>

<script>
	export default {
		props: ['target'],
		methods: {
			goBack() {
				this.target ? this.$router.push(this.target) : this.$router.go(-1);
			},
		},
	};
</script>

<style scoped>
	.btn-back {
		position: absolute;
		width: 90px;
		top: 10px;
		left: 10px;
	}

	.btn-back:hover {
		opacity: 0.7;
		cursor: pointer;
	}

	.btn-back:active {
		transform: scale(0.98);
	}
</style>
