<template>
	<div class="container">
		<home-button routename="/" />
		<div class="learning-container" @click="playLearningAudio">
			<base-circle :isCropped="false">
				<div class="alphabet"></div>
			</base-circle>
			<p>Учене</p>
		</div>
		<div class="test-container" @click="playTestAudio">
			<base-circle :isCropped="false">
				<div class="pencil"></div>
			</base-circle>
			<p>Тестове</p>
		</div>
	</div>
</template>

<script>
	import HomeButton from '../../components/HomeButton.vue';
	import BaseCircle from '../../components/BaseCircle.vue';
	import LearningSound from '../../assets/audio/Буквите.mp3';
	import TestSound from '../../assets/audio/БуквиУчи.mp3';

	export default {
		components: {
			BaseCircle,
			HomeButton,
		},
		data() {
			return {
				learningSound: new Audio(LearningSound),
				testSound: new Audio(TestSound),
			};
		},
		mounted() {
			this.learningSound.addEventListener('ended', () => {
				this.$router.push('/letters');
			});
			this.testSound.addEventListener('ended', () => {
				this.$router.push('/test');
			});
		},
		methods: {
			playLearningAudio() {
				this.learningSound.play();
			},
			playTestAudio() {
				this.testSound.play();
			},
		},
	};
</script>

<style scoped>
	.container {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100vw;
		height: 100vh;
		background-image: url('../../assets/menu/background.png');
		background-position: center center;
		background-size: cover;
	}

	.container p {
		margin-top: 20px;
		font-size: 2.5rem;
		cursor: default;
	}

	.learning-container {
		margin-right: 20vw;
		position: relative;
	}

	.test-container {
		position: relative;
	}

	.alphabet {
		display: block;
		position: absolute;
		width: 60%;
		height: 60%;
		background: url('../../assets/menu/ABC.png');
		background-position: center center;
		background-size: contain;
		background-repeat: no-repeat;
	}

	.pencil {
		display: block;
		position: absolute;
		width: 45%;
		height: 45%;
		background: url('../../assets/menu/pencil.png');
		background-position: center center;
		background-size: contain;
		background-repeat: no-repeat;
	}
</style>
