import { createRouter, createWebHashHistory } from 'vue-router';
import Home from '../views/Main/Home.vue';
import Menu from '../views/Main/Menu.vue';
import TestMenu from '../views/Main/TestMenu.vue';
import Letters from '../views/Learning/Letters.vue';
import LetterDetails from '../views/Learning/LetterDetails.vue';
import Words from '../views/Learning/Words.vue';
import GuessPosition from '../views/Quiz/GuessPosition.vue';
import GuessPicture from '../views/Quiz/GuessPicture.vue';
import GuessLetter from '../views/Quiz/GuessLetter.vue';
import Result from '../views/Quiz/Result.vue';

const routes = [
	{
		path: '/',
		name: 'Home',
		component: Home,
	},
	{
		path: '/menu',
		name: 'Menu',
		component: Menu,
	},
	{
		path: '/letters',
		name: 'Letters',
		component: Letters,
	},
	{
		path: '/letters/:key',
		name: 'LetterDetails',
		component: LetterDetails,
	},
	{
		path: '/letters/:key/:wordId',
		name: 'Words',
		component: Words,
	},
	{
		path: '/test',
		name: 'TestMenu',
		component: TestMenu,
	},
	{
		path: '/test/position',
		name: 'GuessPosition',
		component: GuessPosition,
	},
	{
		path: '/test/picture',
		name: 'GuessPicture',
		component: GuessPicture,
	},
	{
		path: '/test/letter',
		name: 'GuessLetter',
		component: GuessLetter,
	},
	{
		path: '/test/result',
		name: 'Result',
		component: Result,
	},
];

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes,
});

export default router;
