<template>
	<div
		class="circle-container"
		@mouseenter="changeSpeed"
		@mouseleave="changeSpeed"
		:style="scaleString"
	>
		<div
			class="background-image"
			:class="croppedClass"
			:style="degreeString"
		></div>
		<slot></slot>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				degrees: 0,
				isSlow: true,
				croppedClass: '',
			};
		},
		props: {
			isCropped: Boolean,
		},
		mounted() {
			this.croppedClass = this.isCropped
				? 'background-cropped-sun'
				: 'background-sun';
			setInterval(() => {
				this.degrees += this.isSlow ? 0.5 : 1;
			}, 10);
		},
		methods: {
			changeSpeed() {
				this.isSlow = !this.isSlow;
			},
		},
		computed: {
			degreeString() {
				return `transform: rotate(${this.degrees}deg)`;
			},
			scaleString() {
				let scaleString = this.isSlow ? 'scale(1)' : 'scale(1.1)';
				return `transform: ${scaleString}`;
			},
		},
	};
</script>

<style scoped>
	.circle-container {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 350px;
		height: 350px;
	}

	.background-image {
		width: 100%;
		height: 100%;
		background-position: center center;
		background-size: cover;
	}

	.background-sun {
		background-image: url('../assets/menu/sun.png');
	}

	.background-cropped-sun {
		background-image: url('../assets/testmenu/sun.png');
	}

	.circle-container:hover {
		cursor: pointer;
	}
</style>
