import alphabetDetails from './alphabetDetails';
import _ from 'underscore';

export const getGuessPicture = () => {
	// Remove excluded alphabet
	let filteredAlphabet = alphabetDetails.filter(
		(alphabet) => !(alphabet.alphabet == 'ь')
	);

	let questions = [];
	// Create question
	for (let i = 0; i < filteredAlphabet.length; i++) {
		// Get correct answer
		let correctAnswer = filteredAlphabet[i];

		// Generate answers
		let answers = [];
		answers.push(correctAnswer.alphabet);
		let otherAnswers = _.shuffle(
			filteredAlphabet.filter(
				(alphabet) => !(alphabet.alphabet == correctAnswer.alphabet)
			)
		)
			.slice(0, 3)
			.map((alphabet) => alphabet.alphabet);
		answers = [...answers, ...otherAnswers];

		// Get random word
		let words = correctAnswer.words.filter(
			(word) => word[0] == correctAnswer.alphabet
		);

		questions.push({
			correctAnswer: correctAnswer.alphabet,
			answers: _.shuffle(answers),
			word: _.shuffle(words)[0],
		});
	}

	return _.shuffle(questions);
};

export const getGuessPosition = () => {
	// Remove excluded alphabet
	let filteredAlphabet = alphabetDetails.filter(
		(alphabet) => !(alphabet.alphabet == 'ь')
	);

	let questions = [];
	// Create question
	for (let i = 0; i < filteredAlphabet.length; i++) {
		// Get correct answer
		let correctAnswer = filteredAlphabet[i];

		// Generate answers
		let answers = [];
		let correctAnswerWord = _.shuffle(
			correctAnswer.words.filter((word) => correctAnswer.alphabet == word[0])
		)[0];
		answers.push(correctAnswerWord);

		let otherAnswers = _.shuffle(filteredAlphabet)
			.slice(0, 3)
			.map(
				(alphabet) =>
					_.shuffle(
						alphabet.words.filter((word) => alphabet.alphabet == word[0])
					)[0]
			);
		answers = [...answers, ...otherAnswers];

		questions.push({
			correctAnswer: correctAnswerWord,
			answers: _.shuffle(answers),
			alphabet: correctAnswer.alphabet,
		});
	}

	return _.shuffle(questions);
};
