<template>
	<img
		class="btn-home"
		src="../assets/Home.png"
		alt="Home Button"
		@click="goToHome"
	/>
</template>

<script>
	export default {
		props: {
			routename: {
				type: String,
				default: '/menu',
			},
		},
		methods: {
			goToHome() {
				this.$router.push(this.routename);
			},
		},
	};
</script>

<style scoped>
	.btn-home {
		position: absolute;
		width: 90px;
		height: 90px;
		top: 10px;
		right: 10px;
	}

	.btn-home:hover {
		opacity: 0.7;
		cursor: pointer;
	}

	.btn-home:active {
		transform: scale(0.98);
	}
</style>
