<template>
	<img :src="src" :class="direction" alt="next arrow" :style="widthStyle" />
</template>

<script>
	export default {
		props: {
			width: Number,
			direction: {
				type: String,
				default: 'left',
			},
		},
		data() {
			return {
				widthStyle: '',
				src: '',
			};
		},
		mounted() {
			this.widthStyle = `width: ${this.width}px`;
			this.src =
				this.direction === 'right'
					? require('../assets/Nextarrow.png')
					: require('../assets/Prevarrow.png');
		},
	};
</script>

<style scoped>
	img {
		position: absolute;
		width: 100px;
		cursor: pointer;
	}

	img:active {
		transform: scale(0.98);
	}

	.left {
		left: 20px;
	}

	.right {
		right: 20px;
	}
</style>
