<template>
	<div class="container">
		<back-button :target="backUrl" />
		<home-button />
		<template v-if="wordId > 0">
			<nav-button :width="150" direction="left" @click="goToPrevWord" />
		</template>
		<template v-if="wordId < alphabetLength - 1">
			<nav-button :width="150" direction="right" @click="goToNextWord" />
		</template>
		<speaker-button @click="playAudio" />

		<div class="content-container">
			<div class="letter-container">
				<img class="letter-image" :src="simpleSrc" alt="letter" />
			</div>
			<div class="details">
				<div class="image-container">
					<img class="word-image" :src="wordSrc" alt="" />
					<letter-map
						:word="word"
						:alphabet="currentAlphabet.alphabet"
						:type="wordType"
					/>
				</div>
				<div class="words-container">
					<p><span v-html="formattedWord"></span></p>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import BackButton from '../../components/BackButton.vue';
	import HomeButton from '../../components/HomeButton.vue';
	import NavButton from '../../components/NavButton.vue';
	import SpeakerButton from '../../components/SpeakerButton.vue';
	import LetterMap from '../../components/LetterMap.vue';
	import alphabetDetails from '../../utils/alphabetDetails';
	export default {
		components: {
			BackButton,
			HomeButton,
			NavButton,
			SpeakerButton,
			LetterMap,
		},
		data() {
			return {
				key: '',
				word: '',
				wordId: '',
				wordType: '',
				wordSrc: '',
				wordAudio: null,
				formattedWord: '',
				simpleSrc: '',
				backUrl: '',
				alphabetLength: 0,
				currentAlphabet: {},
			};
		},
		methods: {
			goToPrevWord() {
				this.$router.push(
					`/letters/${this.$route.params.key}/${+this.wordId - 1}`
				);
			},
			goToNextWord() {
				this.$router.push(
					`/letters/${this.$route.params.key}/${+this.wordId + 1}`
				);
			},
			playAudio() {
				this.wordAudio.play();
			},
		},
		mounted() {
			this.key = this.$route.params.key;
			this.wordId = this.$route.params.wordId;
			this.simpleSrc = require(`../../assets/simple-letters/${this.key}.png`);

			this.currentAlphabet = alphabetDetails.find(
				(letter) => letter.alphabet === this.key
			);
			this.backUrl = `/letters/${this.currentAlphabet.alphabet}`;
			this.alphabetLength = this.currentAlphabet.words.length;
			this.wordType = this.currentAlphabet.type;

			let isLetterFound = false;

			// Mark specific alphabet with color
			this.word = this.currentAlphabet.words[this.wordId];
			this.formattedWord = this.word
				.split('')
				.map((char) => {
					if (
						!isLetterFound &&
						(char === this.currentAlphabet.alphabet ||
							char === this.currentAlphabet.alphabet.toLowerCase())
					) {
						isLetterFound = true;
						if (this.currentAlphabet.type === 'vowel') {
							return `<span style="color: #FF2D2D">${char}</span>`;
						} else {
							return `<span style="color: #6DB5FF">${char}</span>`;
						}
					} else {
						return char;
					}
				})
				.join('');

			this.wordSrc = require(`../../assets/words/${
				this.currentAlphabet.words[this.wordId]
			}.jpg`);

			this.wordAudio = new Audio(
				require(`../../assets/audio/Думи/${
					this.currentAlphabet.words[this.wordId]
				}.mp3`)
			);

			this.playAudio();
		},
		updated() {
			// console.log('UPDATED');
		},
	};
</script>

<style scoped>
	.container {
		width: 100vw;
		height: 100vh;
		padding-top: 5vh;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		background-image: url('../../assets/yellow-bg.png');
		background-repeat: no-repeat;
		background-position: center center;
		background-size: cover;
		overflow: scroll;
	}

	.letter-image {
		width: 80%;
		flex: 1;
	}

	.details {
		flex: 2;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	.content-container {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 70%;
	}

	.letter-container {
		display: flex;
		justify-content: center;
		align-items: flex-start;
		height: 100%;
	}

	.image-container {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		width: 50%;
		padding: 20px;
		border-radius: 40px;
	}

	.word-image {
		border-radius: 40px;
		width: 550px;
	}

	.words-container {
		font-size: 4rem;
		color: #53b027;
	}
</style>
