<template>
	<div class="container">
		<home-button />
		<back-button target="/letters" />
		<nav-button
			v-if="isNotEmpty"
			:width="150"
			direction="right"
			@click="goToWords"
		/>
		<img :src="letter" alt="letter" />
		<speaker-button @click="playAudio" />
	</div>
</template>

<script>
	import HomeButton from '../../components/HomeButton.vue';
	import BackButton from '../../components/BackButton.vue';
	import NavButton from '../../components/NavButton.vue';
	import SpeakerButton from '../../components/SpeakerButton.vue';

	import alphabetDetails from '../../utils/alphabetDetails';

	export default {
		components: {
			HomeButton,
			BackButton,
			NavButton,
			SpeakerButton,
		},
		data() {
			return {
				letter: '',
				letterAudio: null,
				currentAlphabet: null,
			};
		},
		mounted() {
			let key = this.$route.params.key;
			this.currentAlphabet = alphabetDetails.find(
				(item) => item.alphabet === key
			);
			this.letter = require(`../../assets/letters/${key}.png`);
			this.letterAudio = new Audio(
				require(`../../assets/audio/АБВ/${key}.mp3`)
			);

			this.playAudio();
		},
		methods: {
			playAudio() {
				this.letterAudio.play();
			},
			goToWords() {
				this.$router.push(`/letters/${this.$route.params.key}/0`);
			},
		},
		computed: {
			isNotEmpty() {
				return this.currentAlphabet?.words?.length > 0;
			},
		},
	};
</script>

<style scoped>
	.container {
		width: 100vw;
		height: 100vh;
		padding-top: 5vh;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		background-image: url('../../assets/yellow-bg.png');
		background-repeat: no-repeat;
		background-position: center center;
		background-size: cover;
		overflow: scroll;
	}
</style>
