<template>
	<div class="label">
		<div
			v-for="(item, index) in collection"
			:key="item.letter + index"
			class="label-position"
			:class="item.marked ? type : ''"
		>
			{{ item.letter }}
		</div>
	</div>
</template>

<script>
	export default {
		props: {
			show: {
				type: Boolean,
				default: true,
			},
			type: {
				type: String,
				required: true,
			},
			word: {
				type: String,
				required: true,
			},
			alphabet: {
				type: String,
				required: true,
			},
		},
		data() {
			return {};
		},
		computed: {
			collection() {
				const splittedWord = this.word.split('');
				const position =
					splittedWord.indexOf(this.alphabet) !== -1
						? splittedWord.indexOf(this.alphabet)
						: splittedWord.indexOf(this.alphabet?.toLowerCase());

				return splittedWord.map((letter, index) => {
					const isMarked = index === position;
					if (isMarked) {
						return {
							letter: this.show ? letter : '',
							marked: isMarked,
						};
					} else {
						return {
							letter: letter,
							marked: isMarked,
						};
					}
				});
			},
		},
	};
</script>

<style scoped>
	.label {
		display: flex;
		position: relative;
		margin-top: 10px;
		font-size: 2em;
		min-width: 400px;
		height: 50px;
		background: white;
	}

	.label-position {
		display: flex;
		justify-content: center;
		align-items: center;
		flex: 1;
		border: 1px solid black;
	}

	.label-position.vowel {
		background: #ff2d2d;
		color: white;
	}

	.label-position.consonant {
		background: #6db5ff;
		color: white;
	}
</style>
