<template>
	<div class="container">
		<div class="content-container">
			<home-button />
			<header class="header">
				Да учим буквите
			</header>
			<div class="letters-container">
				<letter-button
					v-for="letter in letterCollection"
					:key="letter.key"
					@click="goToLetterDetails(letter.key)"
				>
					<img class="card-image" :src="letter.value" alt="Letter" />
				</letter-button>
			</div>
		</div>
	</div>
</template>

<script>
	import HomeButton from '../../components/HomeButton.vue';
	import LetterButton from '../../components/LetterButton.vue';
	import alphabetDetails from '../../utils/alphabetDetails';

	export default {
		components: {
			HomeButton,
			LetterButton,
		},
		data() {
			return {
				letterCollection: [],
			};
		},
		mounted() {
			this.letterCollection = alphabetDetails.map((letter) => ({
				key: letter.alphabet,
				value: require(`../../assets/letters/${letter.alphabet}.png`),
			}));
		},
		methods: {
			goToLetterDetails(key) {
				this.$router.push(`/letters/${key}`);
			},
		},
	};
</script>

<style scoped>
	.container {
		width: 100vw;
		height: 100vh;
		padding-top: 5vh;
		display: flex;
		flex-direction: column;
		align-items: center;
		background-image: url('../../assets/yellow-bg.png');
		background-repeat: no-repeat;
		background-position: center center;
		background-size: cover;
		overflow: scroll;
	}

	.content-container {
		width: 70%;
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	.header {
		font-size: 2.5rem;
		color: white;
		width: 100%;
		padding: 20px;
		background: #f8ab65;
		border-radius: 15px;
		box-shadow: 3px 3px 0px rgba(3, 3, 3, 0.3);
		text-shadow: 2px 3px 2px #ff7a00;
		cursor: default;
	}

	.letters-container {
		display: grid;
		justify-content: center;
		grid-template-columns: repeat(5, 1fr);
		gap: 40px;
		justify-items: center;
		margin-top: 20px;
		padding-bottom: 3vh;
		width: 100%;
		overflow: hidden;
	}

	.card-image {
		width: 100%;
		height: 100%;
	}
</style>
