<template>
	<div class="container">
		<home-button />
		<back-button target="/test" />
		<speaker-button @click="playAudio" />
		<score-counter :current="currentAnswer" :max="maxAnswer" />
		<div v-if="!isClickable" class="content-overlay"></div>
		<div class="content">
			<div
				class="alphabet-question"
				:style="{ 'background-image': 'url(' + questionImageSrc + ')' }"
				alt="Alphabet Question"
			/>
			<div class="image-answers">
				<img
					class="image-answer"
					v-for="(answerImageSrc, index) in answersImageSrc"
					:class="!isClickable ? answerImageSrc.status : ''"
					:key="answerImageSrc.answer + index"
					:src="answerImageSrc.src"
					:alt="'answer' + index"
					@click="answerHandler(answerImageSrc.answer)"
				/>
			</div>
		</div>
	</div>
</template>

<script>
	import HomeButton from '../../components/HomeButton.vue';
	import BackButton from '../../components/BackButton.vue';
	import SpeakerButton from '../../components/SpeakerButton.vue';
	import ScoreCounter from '../../components/ScoreCounter.vue';

	import ChooseAudio from '../../assets/audio/ИзбериБуквичка.mp3';
	import { getGuessPosition } from '../../utils/getQuestion.js';

	export default {
		data() {
			return {
				chooseAudio: new Audio(ChooseAudio),
				remainingQuestions: [],
				maxAnswer: 3,
				currentAnswer: 0,
				correctAnswerCounter: 0,
				isClickable: true,
			};
		},
		components: {
			HomeButton,
			BackButton,
			SpeakerButton,
			ScoreCounter,
		},
		methods: {
			playAudio() {
				this.chooseAudio.play();
			},
			answerHandler(answer) {
				this.isClickable = false;
				this.currentAnswer++;
				if (this.currentQuestion.correctAnswer == answer) {
					this.correctAnswerCounter++;
				}

				setTimeout(() => {
					this.remainingQuestions.shift();
					if (this.currentAnswer === this.maxAnswer) {
						this.$router.push({
							path: '/test/result',
							query: {
								correct: this.correctAnswerCounter,
								incorrect: this.maxAnswer - this.correctAnswerCounter,
							},
						});
					}
					this.isClickable = true;
				}, 2000);
			},
		},
		computed: {
			currentQuestion() {
				return this.remainingQuestions[0];
			},
			questionImageSrc() {
				return require(`../../assets/letters/${this.remainingQuestions[0].alphabet}.png`);
			},
			answersImageSrc() {
				return this.remainingQuestions[0].answers.map((answer) => ({
					answer: answer,
					src: require(`../../assets/words/${answer}.jpg`),
					status:
						this.remainingQuestions[0].correctAnswer == answer
							? 'correct'
							: 'wrong',
				}));
			},
		},
		mounted() {
			this.playAudio();
		},
		created() {
			this.remainingQuestions = getGuessPosition();
		},
	};
</script>

<style scoped>
	.container {
		width: 100vw;
		height: 100vh;
		padding-top: 5vh;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		align-items: center;
		background-image: url('../../assets/yellow-bg.png');
		background-repeat: no-repeat;
		background-position: center center;
		background-size: cover;
		overflow: scroll;
	}

	.content {
		width: 90%;
		height: 100%;
		display: grid;
		grid-template-rows: 4fr 5fr;
		justify-content: center;
		/* flex-direction: column; */
	}

	.content-overlay {
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		cursor: not-allowed;
		z-index: 10;
	}

	.alphabet-question {
		margin-right: 20px;
		background-size: contain;
		background-position: center center;
		background-repeat: no-repeat;
		flex: 1;
	}

	.image-answers {
		display: grid;
		justify-content: center;
		align-items: center;
		grid-template-columns: 1fr 1fr;
		width: 400px;
		gap: 20px;
	}

	.image-answer {
		border: 1px solid black;
		width: 300px;
		height: 150px;
		object-fit: contain;
	}

	.image-answer:hover {
		cursor: pointer;
	}

	.image-answer:active {
		transform: scale(0.98);
	}

	.correct {
		position: relative;
		animation: upAndDown 1s alternate infinite;
	}

	.wrong {
		opacity: 0.2;
	}

	@keyframes upAndDown {
		0% {
			top: 0;
		}
		50% {
			top: 20px;
		}
		100% {
			top: -20px;
		}
	}
</style>
